import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import Kpi from '../../components/kpi/Kpi';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';

import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';


const OverviewContent = (props) => {
  const { overviewData, selectedRangeValue } = props;

  const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  //by segment
  const overallSegment = overviewData ? overviewData.overall : [];
  const percentofGrossBySegment = overviewData ? overviewData.percent_of_gross_by_segment ? overviewData.percent_of_gross_by_segment : overviewData.percent_of_gross_by_segment : [];
  const segmentRevenueMixSegment = overviewData ? overviewData.cross_border_bkgs_mix_percent_by_segment : [];
  const crossBorderBKGSBySegment = overviewData ? overviewData.cross_border_bkgs_by_segment : [];
  const segmentDetailsSegment = overviewData ? overviewData.segment_details : [];
  //by vertical
  // const overallVertical = overviewData ? overviewData.by_vertical.overall : [];
  // const grossBookingsByVertical = overviewData ? overviewData.by_vertical.gross_bookings_by_vertical ? overviewData.by_vertical.gross_bookings_by_vertical : overviewData.by_vertical.gross_bookings_by_vertical : [];
  // const segmentTrendsVertical = overviewData ? overviewData.by_vertical.segment_trends ? overviewData.by_vertical.segment_trends : overviewData.by_vertical.segment_trends : [];
  // const segmentMixVertical = overviewData ? overviewData.by_vertical.segment_revenue_mix : [];
  // const segmentDetailsVertical = overviewData ? overviewData.by_vertical.segment_details : [];

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const defaultSorted = [
    {
      dataField: 'mix_percent',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_of_gross',
      text: '% of GROSS',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    // {
    //   dataField: '',
    //   text: '%BUDGET',
    //   sort: true,
    //   align: 'center',
    //   headerAlign: 'center'
    // },
    // {
    //   dataField: '',
    //   text: '%MPQ',
    //   sort: true,
    //   align: 'center',
    //   headerAlign: 'center'
    // }
  ];

  return (
    <Container className="overview" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <div className="fix-width">
          <Card className="TotalRevenue revenue " >
            <Card.Body>
              <Card.Title>cross border bkgs</Card.Title>
              <KPI_NEW
                totalValue={overallSegment.total.value}
                totalUnit={overallSegment.total.unit}
                details={overallSegment.details}
              />
              {/* <Kpi
                data={overall_segment}
                total={overall_segment.total}
                withPercent={true}
              /> */}
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>cross border bkgs by segment</Card.Title>
              <div className="d-flex">
                <Legend legend={crossBorderBKGSBySegment.legend} />
                {
                  selectedRangeValue === "last_completed_quarter" ?
                    <BasicDD
                      className=" ml-auto"
                      id="overviewStackedColumn"
                      options={optionOverviewFilter}
                      value={stackedColumnSelection}
                      setValue={setStackedColumnSelection}
                    /> : null
                }
              </div>
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="crossBorderBKGSBySegment_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  isOverviewChart={true}
                  isGrowthPercentToggable={true}
                  stackedColumnSelection={stackedColumnSelection.key}
                  selectedRangeValue={selectedRangeValue}
                  data={crossBorderBKGSBySegment.data}
                  legend={crossBorderBKGSBySegment.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col sm={12} md={3} lg={3}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>cross border bkgs mix % by segment</Card.Title>
              <Legend legend={segmentRevenueMixSegment.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"customer_revenue_overview_donut_chart"}
                  totalTitle={"CROSS BORDER BKGS"}
                  total={segmentRevenueMixSegment.cross_border_bkgs}
                  data={segmentRevenueMixSegment.data}
                  legend={segmentRevenueMixSegment.legend}
                  isDollar={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details scrollable'
                data={segmentDetailsSegment === undefined ? [] : segmentDetailsSegment}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={12} md={3} lg={3}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>% of gross by segment</Card.Title>
              <Legend legend={percentofGrossBySegment.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"cross_border_trade_overview_line_chart"}
                  data={percentofGrossBySegment.data}
                  legend={percentofGrossBySegment.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default OverviewContent;