import React, { useState } from 'react';
import { Switch, Route } from "react-router-dom";

import Sidebar from '../sidebar/Sidebar';
import Topbar from '../topbar/Topbar';

import Home from '../../domain/home/Home';
import ExecutiveCockpit from '../../domain/executiveCockpit/ExecutiveCockpit';
import Bookings from '../../domain/bookings/Bookings';
import TotalRevenue from '../../domain/totalRevenue/TotalRevenue';
import CustomerRevenue from '../../domain/customerRevenue/CustomerRevenue';
import ChannelPerformance from '../../domain/channelPerformance/ChannelPerformance';
import GrossBookings from '../../domain/grossBookings/GrossBookings';
import NetBookings from '../../domain/netBookings/NetBookings';
import Pricing from '../../domain/pricing/Pricing';
import CrossBorderTrade from '../../domain/crossBorderTrade/CrossBorderTrade';
import CustomerRevenueLive from '../../domain/customerRevenueLive/CustomerRevenue';
import Snapshot from '../../domain/snapshot/Snapshot';
import DataMart from '../../domain/dataMart/DataMart';
import AccountNavigator from '../../domain/accountNavigator/AccountNavigator';
import AnalyticsDashboard from '../../domain/analyticsDashboard/AnalyticsDashboard';
import HelpCenter from '../../domain/helpCenter/HelpCenter';
import Revenue from '../../domain/revenue/Revenue';
import Churn from '../../domain/churn/Churn';
import './dashboard.scss';


const Dashboard = () => {
  const [open, setOpen] = useState(true);

  const toggleOpen = () => {
    setOpen(!open);
  }

  return (
    <div className="dashboard">
      <Sidebar open={open} toggleOpen={toggleOpen} />
      <div className={`main-content ${open === true ? 'open' : 'closed'}`}>
        <Topbar />
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/executive-cockpit/metrics" component={ExecutiveCockpit} />
          <Route path="/executive-cockpit/metro" component={ExecutiveCockpit} />
          <Route path="/executive-cockpit/topline" component={ExecutiveCockpit} />
          <Route path="/executive-cockpit/total-revenue/overview" component={TotalRevenue} />
          <Route path="/executive-cockpit/total-revenue/growth" component={TotalRevenue} />
          <Route path="/executive-cockpit/total-revenue/metro" component={TotalRevenue} />
          <Route path="/executive-cockpit/customer-revenue/overview" component={CustomerRevenue} />
          <Route path="/executive-cockpit/customer-revenue/growth" component={CustomerRevenue} />
          <Route path="/executive-cockpit/customer-revenue/metro" component={CustomerRevenue} />
          <Route path="/executive-cockpit/customer-revenue/customer" component={CustomerRevenue} />
          <Route path="/executive-cockpit/channel-performance/overview" component={ChannelPerformance} />
          <Route path="/executive-cockpit/channel-performance/growth" component={ChannelPerformance} />
          <Route path="/executive-cockpit/channel-performance/metro" component={ChannelPerformance} />
          <Route path="/executive-cockpit/channel-performance/partner" component={ChannelPerformance} />
          <Route path="/executive-cockpit/churn/overview" component={Churn} />
          <Route path="/executive-cockpit/churn/growth" component={Churn} />
          <Route path="/executive-cockpit/churn/metro" component={Churn} />
          <Route path="/executive-cockpit/churn/customer" component={Churn} />
          <Route path="/executive-cockpit/gross-bookings-revenue/overview" component={GrossBookings} />
          <Route path="/executive-cockpit/gross-bookings-revenue/growth" component={GrossBookings} />
          <Route path="/executive-cockpit/gross-bookings-revenue/metro" component={GrossBookings} />
          <Route path="/executive-cockpit/gross-bookings-revenue/customer" component={GrossBookings} />
          <Route path="/executive-cockpit/net-bookings-revenue/overview" component={NetBookings} />
          <Route path="/executive-cockpit/net-bookings-revenue/growth" component={NetBookings} />
          <Route path="/executive-cockpit/net-bookings-revenue/metro" component={NetBookings} />
          <Route path="/executive-cockpit/net-bookings-revenue/customer" component={NetBookings} />
          <Route path="/executive-cockpit/pricing/overview" component={Pricing} />
          <Route path="/executive-cockpit/pricing/growth" component={Pricing} />
          <Route path="/executive-cockpit/pricing/lfp" component={Pricing} />
          <Route path="/executive-cockpit/pricing/customer" component={Pricing} />
          <Route path="/executive-cockpit/cross-border-trade/overview" component={CrossBorderTrade} />
          <Route path="/executive-cockpit/cross-border-trade/growth" component={CrossBorderTrade} />
          <Route path="/executive-cockpit/cross-border-trade/import" component={CrossBorderTrade} />
          <Route path="/executive-cockpit/cross-border-trade/export" component={CrossBorderTrade} />
          <Route path="/executive-cockpit/customer-revenue-live/overview" component={CustomerRevenueLive} />
          <Route path="/executive-cockpit/customer-revenue-live/growth" component={CustomerRevenueLive} />
          <Route path="/executive-cockpit/customer-revenue-live/metro" component={CustomerRevenueLive} />
          <Route path="/executive-cockpit/customer-revenue-live/customer" component={CustomerRevenueLive} />
          <Route path="/executive-cockpit/bookings/overview" component={Bookings} />
          <Route path="/executive-cockpit/bookings/growth" component={Bookings} />
          <Route path="/executive-cockpit/bookings/metro" component={Bookings} />
          <Route path="/executive-cockpit/bookings/customer" component={Bookings} />
          <Route path="/executive-cockpit/bookings/additional-resources" component={Bookings} />
          <Route path="/executive-cockpit/revenue/overview" component={Revenue} />
          <Route path="/executive-cockpit/revenue/growth" component={Revenue} />
          <Route path="/executive-cockpit/revenue/metro" component={Revenue} />
          <Route path="/executive-cockpit/revenue/customer" component={Revenue} />
          <Route path="/snapshot-builder/builder" component={Snapshot} />
          <Route path="/snapshot-builder/view-report" component={Snapshot} />
          <Route path="/self-service-rerport/generator" component={DataMart} />
          <Route path="/self-service-rerport/blank-report" component={DataMart} />
          <Route path="/self-service-rerport/blank-report/generated-report" component={DataMart} />
          <Route path="/account-navigator/navigator" component={AccountNavigator} />
          <Route path="/account-navigator/navigator/financials" component={AccountNavigator} />
          <Route path="/analytics-dashboard/page" component={AnalyticsDashboard} />
          <Route path="/help-center" component={HelpCenter} />
          <Route path="*" component={() => "404 NOT FOUND"} />
        </Switch>
      </div>
    </div>
  );
}

export default Dashboard;
