import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import Import from './Import';
import Export from './Export';

import './crossBorderTrade.scss';


const CrossBorderTrade = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Overview')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
      path: '/executive-cockpit/metrics',
      label: 'EXECUTIVE COCKPIT'
    },
    {
      path: path === '/executive-cockpit/cross-border-trade/overview' ? null : '/executive-cockpit/cross-border-trade/overview',
      label: 'Cross Border Bookings'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/executive-cockpit/cross-border-trade/overview',
      label: 'Overview'
    },
    {
      path: '/executive-cockpit/cross-border-trade/growth',
      label: 'Growth'
    },
    {
      path: '/executive-cockpit/cross-border-trade/import',
      label: 'Import'
    },
    {
      path: '/executive-cockpit/cross-border-trade/export',
      label: 'Export'
    }
  ]

  return (
    <div className="cross-border-bookings-page">
      <PageHeader
        title="CROSS BORDER BOOKINGS"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/executive-cockpit/metrics?card=true"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'overview' && <Overview />}
        {currentPath === 'growth' && <Growth />}
        {currentPath === 'import' && <Import />}
        {currentPath === 'export' && <Export />}
      </div>
    </div>
  );
}

export default CrossBorderTrade;
