import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import CustomTable from '../../components/customTable/CustomTable';
import BubbleChart from '../../components/chart/BubbleChart';
import LineChart from '../../components/chart/LineChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import {BasicDD} from '../../components/customDropdown';
import { optionGrowthFilter2 } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const GrowthContent = (props) => {
  const { bubbleName, selectedData, idState, resultData, selectedBubbleData, setSelectedData, setSelectedBubbleData, setBubbleName, setReferenceID, growthData } = props;

  const bubble_initial_value = optionGrowthFilter2 ? optionGrowthFilter2[0] : null;
  const [bubbleSelection, setBubbleSelection] = useState(bubble_initial_value);

  if (idState) {
    selectedBubbleData.map(item => {
      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      } else {
        if (item.region === 'APAC') {
          item.color = '#CD503C'
        } else if (item.region === 'AMER') {
          item.color = '#DC7431'
        } else if (item.region === 'EMEA') {
          item.color = '#F0CC71'
        }
      }

      return item;
    })
  }

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToWholePercent(cell)}
      </span>
    );
  };

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  };

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let selectedData;

    // for (const key in byRankId) {
    //   const element = byRankId[key];

    //   if (element._id === row._id) {
    //     selectedData = element;
    //   }
    // }

    // if (isSelect) {
    //   console.log(row._id)
    //   console.log(selectedData)
    //   setReferenceID(row._id);
    //   setSelectedData(selectedData);
    //   setSelectedBubbleData(growthData.gross_bookings_revenue_size_and_growth.data);
    //   setBubbleName(row.segment);
    // }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment',
      headerClasses: 'segment'
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      sort: true,
      align: 'right',
      headerAlign: 'right',
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'y_mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'q_to_q_usd_growth',
      text: 'Q-Q $ GROWTH',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'q_mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      formatter: formatWholePercent
    }
  ];

  let segmentRanking = growthData ? growthData.segment_ranking : [];
  let byRankId = growthData ? growthData.segment_ranking.by_rank_id : [];
  let byRank = segmentRanking.by_rank === undefined ? [] : segmentRanking.by_rank;
  let segmentTrends = selectedData ? selectedData.segment_y_by_y_growth_trends : growthData.y_by_y_percent_growth_by_segment;
  let segmentByRegion = selectedData ? selectedData.segment_by_region : growthData.contribution_to_total_usd_by_segment;

  return (
    <Container className="growth" fluid>
      <Row className="contentDivider">
        <Col md="5" xs="12">
          <Card className="revenue">
            <Card.Body>
              <Card.Title>mrc size and growth</Card.Title>
              <div className="d-flex">
                <Legend legend={growthData.mrc_size_and_growth.legend} />
                <BasicDD
                  className=" ml-auto"
                  id="group"
                  addTitleOnWide
                  options={optionGrowthFilter2}
                  value={bubbleSelection}
                  setValue={setBubbleSelection}
                />
              </div>
              <div style={{ height: "92%" }}>
                <BubbleChart
                  id={idState ? bubbleName : bubbleSelection.key ? bubbleSelection.key : "cross_border_bubble_chart"}
                  data={idState ? selectedBubbleData : growthData.mrc_size_and_growth.data}
                  selected={idState}
                  bubbleSelection={bubbleSelection.key}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md="7" xs="12">
          <Row>
            <Col md="12" xs="12" style={{ paddingBottom: "0px" }}>
              <Card className="revenue ">
                <Card.Body>
                  <Card.Title>segment ranking</Card.Title>
                  <CustomTable
                    tableName='segment-ranking scrollable'
                    data={byRank}
                    selectRow={selectRow} // For Selecting Row ===
                    defaultSorted={defaultSorted} // For Initial Sort ===
                    rowClasses={rowClasses}
                    columns={columns}
                  />
                </Card.Body>
              </Card>
            </Col>
            <Col style={{ paddingTop: "30px", paddingBottom: " 0px" }} md="12" xs="12">
              <Row >
                <Col md="6" xs="12" style={{ paddingBottom: "0px" }}>
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title> y/y % growth by segment</Card.Title>
                      <Legend legend={segmentTrends.legend} />
                      <div style={{ height: "350px" }}>
                        <LineChart
                          id={"total_revenue_growth_line_chart"}
                          data={segmentTrends.data}
                          legend={segmentTrends.legend}
                        />
                      </div>
                    </Card.Body>
                  </Card >
                </Col>
                <Col md="6" xs="12" style={{ paddingBottom: "0px" }}>
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title>contribution to total $ by segment</Card.Title>
                      <Legend legend={segmentByRegion.legend} />
                      <div style={{ height: "350px" }}>
                        <StackedColumnChart
                          id="cross_border_segment_by_region_stacked_column_chart"
                          hiddenValueAxis={true}
                          isOneHundredPercent={false}
                          isPercentage={false}
                          largerWidth={true}
                          hideLabelBullet={false}
                          data={segmentByRegion.data}
                          legend={segmentByRegion.legend}
                        />
                      </div>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
};

export default GrowthContent;