/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import OverviewContent from './OverviewContent';
import {RadioDD, CheckboxDD, SearchCheckboxDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionRegion, optionMetro, optionVertical, optionGroup2 } from '../../constants/filterOptions';
import { comments } from '../../constants/comments';
import { API_BASE_URL } from '../../constants/config';


const Overview = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [overviewData, setData] = useState(null);

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true);
  };
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelMeasure();
    setShowFilter(false);
  };
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyMeasure();
    setShowFilter(false);
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // checkbox for region values
  const [region_val, set_region_val] = useState([]);
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val, set_metro_val] = useState([]);
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val);
  };
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply);
  };

  // Axios call
  const fetchData = () => {

    let url = `${API_BASE_URL}/gross-bookings-revenue/overview.json?print=pretty`;
    //Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/customerrevenueoverview`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&vertical=${vertical_val_onApply}&groupby=${group2_val_onApply}`;
    // let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';
    // let metroParameter = metro_val.length > 0 ? `&metro=${metro_val_onApply.join(",").toString()}` : '';

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      //Uncomment only when API is now available and working
      // url: url + defaultQueryParameters + regionParameter + metroParameter,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url)
      setData(result.data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, [
    region_val_onApply,
    metro_val_onApply,
    measure_val_onApply,
  ]
  );

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;
  console.log('overview', overviewData)
  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`} ref={cardRef}>
        {overviewData ?
          overviewData  ?
            <>
              <FilterContainer
                className={`${width}`}
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters">
                  <CheckboxDD
                    title="Deploy Region"
                    id="region"
                    valueApplied={region_val_onApply}
                    options={optionRegion}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                    onApply={onApplyRegion}
                    onCancel={onCancelRegion}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro"
                    valueApplied={metro_val_onApply}
                    options={optionMetro}
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                    onApply={onApplyMetro}
                    onCancel={onCancelMetro}
                  />
                  <RadioDD
                    title="Measure"
                    id="measure"
                    options={optionMeasure}
                    radioVal={measure_val}
                    setRadioVal={set_measure_val}
                    onApply={onApplyMeasure}
                    onCancel={onCancelMeasure}
                  />
                </div>

                <FilterModal
                  title="Topline"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  <CheckboxDD
                    title="Deploy Region"
                    id="region"
                    options={optionRegion}
                    visibleInModal
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro"
                    options={optionMetro}
                    visibleInModal
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                  />
                  <RadioDD
                    title="Measure"
                    id="measure"
                    options={optionMeasure}
                    visibleInModal
                    radioVal={measure_val}
                    setRadioVal={set_measure_val}
                  />
                </FilterModal>
              </FilterContainer>
              <OverviewContent
                overviewData={overviewData}
                width={width}
              />
            </>
            : <ResourceNotFoundError redirectURL={"/executive-cockpit/gross-bookings-revenue/overview"} />
          : <InternalServerError redirectURL={"/executive-cockpit/gross-bookings-revenue/overview"} ref={cardRef} />}
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Overview;
