import React, { useState, useRef } from 'react';
import { Modal, Button } from 'react-bootstrap';
import { RadioDD } from '../../components/customDropdown';
import SearchCheckbox from '../../components/searchCheckbox/SearchCheckbox';
import { ReactComponent as AnalyticsDashboards } from '../../assets/images/analytics_dashboards.svg';
import { useHistory } from "react-router-dom";

const metricCategory = [
  {
    key: 'topline',
    text: 'Topline'
  },
  {
    key: 'platform',
    text: 'Platform'
  },
  {
    key: 'market',
    text: 'Market'
  },
  {
    key: 'customer',
    text: 'Customer'
  },
  {
    key: 'product',
    text: 'Product'
  },
  {
    key: 'operation',
    text: 'Operation'
  }
]

const AddMetricsModal = (props) => {

  const { show, setModalShow } = props;

  let history = useHistory();

  // radio for metric values
  const metric_initial_value = metricCategory ? metricCategory[0] : null
  const [metric_val_onApply, set_metric_val_onApply] = useState(metric_initial_value);
  const [metric_val, set_metric_val] = useState(metric_initial_value);
  const onApplyMetric = () => {
    set_metric_val_onApply(metric_val)
  }
  const onCancelMetric = () => {
    set_metric_val(metric_val_onApply)
  }

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const [checkBox_val, set_checkBox_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val)
  }
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply)
  }

  const goTo = () => {
    history.push('/executive-cockpit/metro');
  }

  console.log('addmetrics_checkboxval:', checkBox_val)

  return (
    <Modal id="metrics-modal" className="metrics-modal"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      show={show} onHide={() => setModalShow(false)}>
      <Modal.Header>
        <Modal.Title id="contained-modal-title-vcenter">
          choose metrics to add
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="custom-modal">
        <RadioDD
          title="Metric Category"
          id="metric-category"
          options={metricCategory}
          radioVal={metric_val}
          setRadioVal={set_metric_val}
          onApply={onApplyMetric}
          onCancel={onCancelMetric}
        />
        <SearchCheckbox
          searchPlaceholder='Search for Metric'
          id="metro"
          visibleInModal
          options={[
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'total_revenue',
              text: 'Total Revenue'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'customer_revenue',
              text: 'Customer Revenue'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'gross-bookings',
              text: 'Gross Bookings'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'net-bookings',
              text: 'Net Bookings'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'cross-border-trade',
              text: 'Cross Border Trade'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'pricing',
              text: 'Pricing'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'churn',
              text: 'Churn'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'new-logos',
              text: 'New Logos'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'logo-extension',
              text: 'Logo Extension'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'sales-productivity',
              text: 'Sales Productivity'
            },
            {
              icon: <AnalyticsDashboards className="icon" />,
              key: 'ebitda',
              text: 'EBITDA'
            }
          ]}
          checkVal={metro_val}
          setCheckVal={set_metro_val}
          onApply={onApplyMetro}
          onCancel={onCancelMetro}
          checkBoxValue={checkBox_val}
        />

      </Modal.Body>
      <div className="btn-container">
        <Button variant="link" onClick={() => {
          alert('cancel modal')
        }} size="sm">Cancel</Button>
        <Button variant="primary" size="sm" onClick={() => {
          goTo()
        }}>Next</Button>
      </div>

    </Modal>
  );
}

export default AddMetricsModal;